*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}.App{
    background-image: linear-gradient(lightblue,pink,lightyellow);
    height: 100vh;
}
button{
    background-color: lightblue;
    border: 3px solid black;
    border-radius: 30px;
    height: 60px;
    width: 250px;
}
.btn1{
    position: relative;
    top: 60px;
    left: -50px;
}
.btn2{
    position: relative;
    left: 70px;
    top: 60px;
}
button:hover{
    cursor: pointer;
    background-color: cyan;
}
.container{
    width: 500px;
    height: 250px;
    border: 2px solid black;
    background-color: cyan;
    border-radius: 25px;

}
#container1{
    position: relative;
    top: 250px;
    left: 100px;
}
#container2{
    background-color: palevioletred;
    position: relative;
    left: 750px;
}